.rec.rec-carousel-wrapper{
  min-height: 300px;
  margin-bottom: 70px;
  width: 100%;
  position: relative;
  z-index: -1;
  
}
.rec.rec-pagination{
  display: none;
}
.rec.rec-carousel{
  width: 98%;
  
}
.rec.rec-arrow {
 
}
.rec.rec-arrow:hover {
  border-radius: 50%;
  background-color:grey;
}

.rec.rec-arrow:disabled {
 
}
.rec.rec-item-wrapper{
  margin: 0;
  
}
.rec-carousel-item{
  min-width: 280px;
  min-height: 280px;
  
}
.rec-carousel-item:hover{
  
}
.rec-carousel-item:focus {
  outline: none;
  border: none;
}
.rec-item-wrapper:focus {
  outline: none;
  border: none;
}


@media(max-width: 480px){
  .rec.rec-pagination{
    display: inline-block;
  }
  .rec.rec-arrow {
    display: none;
  }
  .rec.rec-dot.rec.rec-dot_active{
    background-color: black!important;
    box-shadow: 0 0 1px 3px grey;
  }
}

