.header{
    height: 60px;
    background-color: rgba(0,0,0,0.3);
    display: grid;
    position: fixed;
    width: 100%;
    z-index: 10;
  }
li{
  list-style-type: none;
}
ul{
  margin-left: 0;
  padding-left: 0; 
}




/* /////////////////////////////////////////////// */

.menuMain {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-left: 9%;
  margin-right: 9%;
}
.leftItem {float: left;}
.rightItem {float: right;}
.navBarLogo {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}
.menuMain a {
  text-decoration: none;
  display: block;
  line-height: 60px;
  padding: 0 20px;
  font-size: 18px;
  letter-spacing: 2px;
  font-family: 'Arimo', sans-serif;
  font-weight: bold;
  color: white;
  transition: .3s linear;
  cursor: pointer;
}


.menuMain a:hover {background: rgba(0,0,0,.3);}
@media (max-width: 830px) {
  .menuMain{
    display: none;
  }
 
  .navBarLogo {
  position: absolute;
  left: 90%;
  top: 50%;
  transform: translate(-50%,-50%);
}

}

