.company{
  background-color: black;
  text-align: center;
  justify-self: center;
  align-self: center;
  height: 100vh;
}
.companyTitle h2{
  font-size: 2rem;
  color: white;
}
.description p{
  color: white;
  margin: 20px;
}
.aboutCompany{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.photoCompany{
  background-color: wheat;
  margin: 20px;
}

@media(max-width: 480px){
  .aboutCompany{
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .photoCompany{
    background-color: wheat;
    margin: 20px;
    height: 350px;
  }
}