.servises{
  display: grid;
  text-align: center;
  justify-self: center;
  align-self: center;
  height: 100vh;
}
.servisesTitle{
  height: 40px;
}
.servisesTitle h2{
  font-size: 2em;
  font-weight: bold;
}

.servisesItem{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
.servisesItem .item{
  max-width: 360px;
  flex-direction: column;
  max-height: 200px;
  padding: 15px;
  display: flex;
  align-items: center;
}
.itemFhoto img{
  width: 50%;
}
.item p {
  margin: 0;
}

.buttonForMore {
  margin-top: 20px;
}
.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

.btnServises{
  border-style: none;
  border-radius: 5px;
  background: #4676D7;
  color: #fff;
  padding: 10px 16px;
  min-width: 140px;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
}
.btnServises, .btnServises:visited, .btnServises:focus, .btnServises:active{
  outline:0 none !important;
}
.btnServises:hover{
  background: #1d49aa;
}

@media(max-width: 480px){
  .servisesItem{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    
  }
  .item:nth-last-child(-n+2) { /* последние 3 потомка */
    display: none;
  }
}