.project{
  text-align: center;
  justify-self: center;
  align-self: center;
  min-height: 400px;
  width: 100%;
  z-index: 5;
}
.projectTitle h2{
  font-size: 2rem;
  margin-bottom: 0;
}

.item{
  /* background-color: violet; */
  min-width: 260px;
  min-height: 260px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  
}

.titleItem {
  align-self: center;
  background-color: skyblue;
  width: 95%;
  margin: 3px;
  height: 90px
}

.servBox{
  position: relative;
  margin: 10px;
}

.itemText{
  display: flex;
  align-self: center;
  position: absolute;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center
  
}

.someText{
  color:white;
  font-size: 26px;
  z-index: 4;
  width: 100%;
  /* max-width: 360px; */
  min-width: 200px; 
}

.item:hover > .itemText{
  
  /* visibility: visible; */
}

.item img{
  width: 100%;
  position: relative;
  z-index: 3;
}

.subTitle{
  margin-bottom: 30px;
  margin-top: 0;
  color: red;
}