.title {
    color: white;
    position: absolute;
    text-align: center;
    justify-self: center;
    align-self: center;
    width: 100%;
    
  }
.title h1{
  font-size: 3rem;
  text-align: center;
  justify-self: center;
  align-self: center;
  margin: 0;
  width: 100%;
  margin-bottom: 10px;
}
.title p{
  font-size: 1.5em;
  max-width: 100%;
  text-align: center;
  justify-self: center;
  align-self: center;
  color: white;
  margin: 0;
  color: red;
}


@media (max-width: 480px){
  .title {
    width: 100%;
    
  }
.title h1{
  font-size: 3.5rem;
  margin: 0;
  width: 100%;
  margin-bottom: 10px;
}
.title p{
  font-size: 1rem;
  max-width: 100%;
  margin: 0;
  margin-left: 5%;
  margin-right: 5%;
  
}
  
}